import { Injectable } from '@angular/core';
import {HttpClient, HttpResponse, HttpHeaders} from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';

import { LogUser, SignUser, UserResponse } from './userface';
import { GuardService } from '../guard/guard.service';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  url: string;

  userData = new BehaviorSubject<UserResponse>({code: null, username: null, email: null, dp: null, _id: null, sex: null, token: null});
  USER$ = this.userData.asObservable();

  USERDATA: UserResponse;


  httpOption = {
    headers: new HttpHeaders({
      'Content-type': 'application/json',
      token: localStorage.token ? JSON.parse(localStorage.token) : '',
    })
  };

  constructor(private Http: HttpClient, private guard: GuardService) {
    // this.url = 'http://192.168.43.41:2050';
  }

  returnUser() {
    return new Observable<UserResponse>( observer => {
      this.Http.get(`/api/returnUser`, this.httpOption)
       .subscribe( (data: UserResponse) => {
        if (data._id) {
          console.log(JSON.stringify(data));
          data.dp = data.dp ? data.dp : data.sex + '.jpg';
          this.userData.next(data);
        }
        observer.next(data);
      });
    });
  }

  userSignup(form: SignUser) {
    return new Observable<UserResponse>( observer => {
      this.Http.post(`/api/signup`, form, this.httpOption)
      .subscribe((data: UserResponse) => {
        if (data.token) {
          localStorage.token = JSON.stringify(data.token);
          this.guard.loggedIn.next(true);
        }
        this.userData.next(data);
        observer.next(data);
      });
    });
  }

  userLogin(form: LogUser) {
    return new Observable<UserResponse>(observer => {
      this.Http.post(`/api/login`, form, this.httpOption)
      .subscribe((data: UserResponse) => {
        if (data.token) {
          localStorage.token = JSON.stringify(data.token);
          this.guard.loggedIn.next(true);
        }
        this.userData.next(data);
        observer.next(data);
      });
    });

  }

  getFollowers(acceptorId: string) {
    return new Observable<any>( observer => {
      this.Http.get(`/api/followers/${acceptorId}`, this.httpOption)
      .subscribe((data: any) => {
        if (data.length > 0) {
          console.log(JSON.stringify(data));
          observer.next(data);
        }
      });
    });

  }

  getFollowing(initiatorId: string) {
    return new Observable<any>( observer => {
      this.Http.get(`/api/following/${initiatorId}`, this.httpOption)
      .subscribe((data: any) => {
        if (data.length > 0) {
          console.log(JSON.stringify(data));
          observer.next(data);
        }
      });
    });
  }
}
