import { Component, OnInit, OnDestroy } from '@angular/core';
import { SocketService } from './../../socket.service';
import { MESSAGE, MessageResponse } from './../../cap';
import { UserResponse } from './../../User/userface';
import { UserService } from './../../User/user.service';
import { RoomFace, MemberFace } from '../roomface';
import { RoomService } from '../room.service';


@Component({
  selector: 'app-room',
  templateUrl: './room.component.html',
  styleUrls: ['./room.component.css']
})
export class RoomComponent implements OnInit, OnDestroy {
  input: string;
  allMessages: MessageResponse[] = [];
  User: UserResponse;
  roomData: RoomFace;
  roomMembers: MemberFace[] = [];

  constructor( private vice: SocketService, private uservice: UserService, private roomvice: RoomService ) { }

  joinRoom() {
    const messageFace: MESSAGE = { senderId: this.User._id, roomId: this.roomData._id, message: 'joined', type: 4, date: new Date() };
    const senser = this.roomMembers.filter(data => data.userId._id === this.User._id );
    if (senser.length < 1) {
      this.roomvice.joinRoom(this.roomData._id).subscribe(data => {
        if (data.length > 0) {
          this.roomMembers = data;
          this.vice.join(messageFace);
        }
      });
    } else {
      alert('already a member');
    }
  }

  leaveRoom() {
    const messageFace: MESSAGE = { senderId: this.User._id, roomId: this.roomData._id, message: 'left', type: 4, date: new Date() };
    const senser = this.roomMembers.filter(data => data.userId._id === this.User._id );
    if (senser.length > 0) {
      this.roomvice.leaveRoom(this.roomData._id, this.User._id ).subscribe( data => {
          this.roomMembers = data;
          this.vice.leave(messageFace);
      });
    } else {
      alert('Yet to be a memeber');
    }
  }

  getLeaveMessage() {
    this.vice.leaveNotification().subscribe( data => {
      this.allMessages.push(data);
    });
  }

  senderName(id: string) {
    if (id && this.roomMembers) {
      const senser = this.roomMembers.filter(data => data.userId._id === id );
      if (senser.length > 0) {
        return  senser[0].userId.username;
      }
    }
  }

  checkMember(id: string): boolean {
    if (id && this.roomMembers) {
      const senser = this.roomMembers.filter(data => data.userId._id === id );
      if (senser.length > 0) {
        return  true;
      }
      return false;
    }
  }

  senderDp(id: string) {
    if (id && this.roomMembers) {
      const senser = this.roomMembers.filter(data => data.userId._id === id );
      if (senser.length > 0) {
        return  senser[0].userId.dp ? senser[0].userId.dp : senser[0].userId.sex + '.jpg';
      }
    }
  }

  sender() {
    if (this.input) {
      const senser = this.roomMembers.filter(data => data.userId._id === this.User._id );
      console.log(JSON.stringify(senser));
      if (senser.length < 1) { return alert('Yet to be a member please join the group to send a message'); }
      const messageFace: MESSAGE = { senderId: this.User._id, roomId: this.roomData._id, message: this.input, type: 2, date: new Date() };
      this.vice.sendMessage(messageFace);
    }
    this.input = '';
  }

getMembers() {
  this.roomvice.allMembers(this.roomData._id).subscribe(members => this.roomMembers = members);
}

  getRoomData() {
    if (localStorage.roomData) {
      return this.roomData = JSON.parse(localStorage.roomData);
    }
    this.roomvice.data$.subscribe((data: RoomFace) => this.roomData = data);
  }

  getUserDetails() {
    this.uservice.USER$.subscribe( data => this.User = data);
  }

  sentMessages() {
    this.vice.getMessage().subscribe(data => {
     return this.allMessages.push(data);
    });
  }

  getJoinMessages() {
    this.vice.joinNotification().subscribe(data => {
      this.allMessages.push(data.message);
      this.roomMembers.push(data.member);
      console.log(JSON.stringify(this.roomMembers));
    });
  }

  getAllMessages() {
    this.roomvice.allMessages(this.roomData._id).subscribe( data => {
      if (data.length > 0) {
        this.allMessages = data;
      }
    } );
  }

  ngOnInit() {
    this.uservice.returnUser().subscribe();
    this.getRoomData();
    this.getUserDetails();
    this.getAllMessages();
    this.sentMessages();
    this.getJoinMessages();
    this.getMembers();

    // this.conceal();
  }

  ngOnDestroy() {
    this.vice.room = false;
  }


}
