import { Component, OnInit } from '@angular/core';
import {FormBuilder, Validators } from '@angular/forms';
import { UserService } from './../user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  processing = false;
  errorMessage: string;
  submitted = false;

  constructor(private fb: FormBuilder, private uservice: UserService, private route: Router ) { }
signup = this.fb.group({
  username: ['', [Validators.required, Validators.minLength(3), Validators.pattern('(^[a-zA-Z])([a-zA-Z0-9_]{2,})$')]],
  email: ['', [Validators.required, Validators.email]],
  password: ['', [Validators.minLength(7), Validators.required, Validators.pattern('(^[a-zA-Z0-9_-]{7,})$')]],
  confirmPassword: ['', [Validators.required]],
  sex: ['', [Validators.required]]
});

get Email() { return this.signup.get('email'); }
get Username() { return this.signup.get('username'); }
get Password() { return this.signup.get('password'); }
get ConfirmPassword() { return this.signup.get('confirmPassword'); }
get Sex() { return this.signup.get('sex'); }


submitForm() {
  this.processing = true;
  this.submitted = true;
  if (this.signup.valid) {
    console.log('processing');
    const form = { username: this.Username.value, email: this.Email.value, password: this.Password.value, sex: this.Sex.value };
    this.uservice.userSignup(form)
    .subscribe( (collector) => {
      if (collector) {
        this.uservice.userData.next(collector);
        console.log(JSON.stringify(collector));
        this.route.navigate(['/dashboard']);
      } else {
        console.log(JSON.stringify(collector));
        console.log('from error scope');
        console.log(collector);
        // this.errorMessage = collector;
      }
    });
  } else {
    return this.errorMessage = 'Incomplete, all the fields are required';
  }
  setTimeout((() => {this.processing = false; }), 3000);
}
  ngOnInit() {
  }

}
