import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { RoomFace, MemberFace, JoinRoomFace } from './roomface';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { MessageResponse, PCMESSAGE } from '../cap';
import { UserResponse } from './../User/userface';
import { PCMESSAGERESPONSE } from 'src/app/cap';

@Injectable({
  providedIn: 'root'
})
export class RoomService {
  url: string;
  data = new BehaviorSubject<RoomFace>({name: null, creator: null, _id: null, logo: null, type: null, date: null});
  data$ = this.data.asObservable();

  PC = new BehaviorSubject<UserResponse>({username: null, code: null, token: null, _id: null, sex: null, email: null, dp: null });
  PC$ = this.PC.asObservable();





  httpOption = {
    headers: new HttpHeaders({
      'Content-type': 'application/json',
      token: localStorage.token ? JSON.parse(localStorage.token) : '',
    })
  };

  constructor(private router: Router, private http: HttpClient) {
    // this.url = 'http://192.168.43.41:2050';
   }

  roomData(room: RoomFace) {
    if (room) {
      room.logo = room.logo ? room.logo : 'group.jpg';
      localStorage.roomData = JSON.stringify(room);
      this.data.next(room);
      this.router.navigate(['/room']);
    }
  }

  pcData( pc: UserResponse ) {
    if (pc) {
      pc.token = null;
      pc.dp = pc.dp ? pc.dp : pc.sex + '.jpg';
      localStorage.pcData = JSON.stringify(pc);
      this.PC.next(pc);
      this.router.navigate(['/privatechat']);
    }
  }

  allPcMessages(pcId: string) {
    return new Observable<PCMESSAGERESPONSE[]>( observer => {
      this.http.get(`/api/getRoomMessages/${pcId}`, this.httpOption)
      .subscribe(( data: PCMESSAGERESPONSE[]) => {
        // console.log(` all messages fetched are ${JSON.stringify(data)}`);
        observer.next(data);
      });
    });
  }

  joinRoom(roomId: string) {
    return new Observable<MemberFace []>( observer => {
      this.http.get(`/api/joinRoom/${roomId}`, this.httpOption)
      .subscribe(( data: MemberFace []) => {
        console.log(` new room member is fetched are ${JSON.stringify(data)}`);
        observer.next(data);
      });
    });
  }

  leaveRoom(roomId: string, userId: string) {
    return new Observable<MemberFace[]>( observer => {
      this.http.get(`/api/leaveRoom/${roomId}/${userId}`, this.httpOption)
      .subscribe(( data: MemberFace[]) => {
        console.log(` leave response is ${JSON.stringify(data)}`);
        observer.next(data);
      });
    });
  }

  allMessages(roomId: string) {
    return new Observable<MessageResponse[]>( observer => {
      this.http.get(`/api/getRoomMessages/${roomId}`, this.httpOption)
      .subscribe(( data: MessageResponse[]) => {
        // console.log(` all messages fetched are ${JSON.stringify(data)}`);
        observer.next(data);
      });
    });
  }

  allMembers(roomId: string) {
    return new Observable<MemberFace[]>( (observer) => {
      this.http.get(`/api/getMembers/${roomId}`, this.httpOption)
      .subscribe((members: MemberFace[]) => {
        console.log(JSON.stringify(members));
        observer.next(members);
      });
    });
  }

  allRooms() {
    return new Observable<RoomFace[]>( (observer) => {
      this.http.get(`/api/getRooms`, this.httpOption)
      .subscribe((rooms: RoomFace[]) => {
        console.log(JSON.stringify(rooms));
        observer.next(rooms);
      });
    });
  }
}


