import { Injectable } from '@angular/core';
import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable, of } from 'rxjs';
import { GuardService } from './guard.service';
import { SignupComponent } from './../User/signup/signup.component';

@Injectable({
  providedIn: 'root'
})
export class ExitGuard implements CanDeactivate<SignupComponent> {
  constructor( private serve: GuardService) {}
  canDeactivate(
    component: SignupComponent, next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | boolean {
      console.log(state.url);
      if ( component.signup.dirty && !component.processing ) {
       return this.serve.leaving();
        // const action = confirm('Discard changes?');
        // return action;
        }
      return true;
  }

}
