import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { UserService } from './../user.service';
import { RoomFace } from 'src/app/Rooms/roomface';
import { RoomService } from 'src/app/Rooms/room.service';
import { UserResponse } from '../userface';
import {FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit, OnDestroy {
  photo: File = null;
  User: UserResponse;
  uploadMessage: string;
  allRooms: RoomFace[];
  createGroupErrorMessage: string;
  startCreateRoom: boolean;
  createGroupSuccessMessage: string;
  notification: any[];
  followers: any[];
  following: any[];




  httpOption = {
    headers: new HttpHeaders({
      'Content-type': 'application/json',
      token: localStorage.token ? JSON.parse(localStorage.token) : '',
    })
  };
  constructor( private http: HttpClient, private fb: FormBuilder, private uservice: UserService, public roomvice: RoomService) { }

  newGroup = this.fb.group({
    groupname: ['', [Validators.required, Validators.minLength(3), Validators.pattern('(^[a-zA-Z])([a-zA-Z0-9_]{2,})$')]],
    type: ['', [Validators.required,  Validators.minLength(1),  Validators.maxLength(1), ]]
  });

  get GroupName() { return this.newGroup.get('groupname');  }
  get Type() {  return this.newGroup.get('type'); }

  Submit() {
    console.log('here');
    if (this.photo) {
      const httpOption = {
        headers: new HttpHeaders({
          // 'Content-type': 'application/json',
          token: localStorage.token ? JSON.parse(localStorage.token) : '',
        })
      };
      try {
        const formMedia = new FormData();
        formMedia.append('image', this.photo);
        this.http.post<any>('/api/upload', formMedia, httpOption)
        .subscribe( data =>  this.User.dp = data.dp);
      } catch (error) {
        console.log(error);
      }
    } else {
      return;
    }
  }

  Group(modal: any) {
    this.startCreateRoom = true;
    this.createGroupErrorMessage = '';
    this.createGroupSuccessMessage = '';
    if (this.newGroup.valid) {
      const formData = { name: this.GroupName.value, type: Number(this.Type.value) };
      this.http.post<RoomFace>('/api/createRoom', formData, this.httpOption )
      .subscribe (data => {
        if (data._id) {
          this.allRooms.push(data);
          this.createGroupSuccessMessage = `${data.name} has been successfuly created`;
          setTimeout((() => modal.style.display = 'none' ), 3000);
        } else {
          this.createGroupErrorMessage = 'Group already exists, please use another name or join the group';
        }
        // return setTimeout((() => this.createGroupErrorMessage = '' ), 3000);
      });
    } else {
      setTimeout((() => this.startCreateRoom = false ), 3000);
      this.createGroupErrorMessage = 'All fields are required';
    }
    this.startCreateRoom = false;
    return setTimeout((() => { this.createGroupErrorMessage = ''; this.createGroupSuccessMessage = ''; } ), 3000);
  }

  fileProgress(file: any) {
    console.log('parsed');
    this.photo = file.target.files[0] as File;
    console.log(`${this.photo.name} and ${this.photo.size} and ${this.photo.type}`);
  }

  getUserDetails() {
    this.uservice.USER$.subscribe(data => {
      data.dp = data.dp ? data.dp : data.sex + '.jpg';
      this.User = data;
      console.log(this.User);
    });
  }

  getAllRooms() {
    this.roomvice.allRooms().subscribe((data: RoomFace[]) => {
      this.allRooms = data;
      // console.log(`all room is ${JSON.stringify(this.allRooms)}`);
    });
  }

  getFollowers() {

  }

  getFollowing() {

  }


  ngOnInit() {
    this.getUserDetails();
    this.getAllRooms();
    this.uservice.returnUser().subscribe();
  }

  ngOnDestroy() {
    // this.uservice.returnUser().unsuscribe()
  }

}
