import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RoomComponent } from './Rooms/room/room.component';
import { LoginComponent } from './User/login/login.component';
import { FogetPasswordComponent } from './User/foget-password/foget-password.component';
import { DashboardComponent } from './User/dashboard/dashboard.component';
import { Room2Component } from './Rooms/room2/room2.component';
import { AuthGuard } from './guard/auth.guard';
import { ExitGuard } from './guard/exit.guard';
import { SignupComponent } from './User/signup/signup.component';




const routes: Routes = [
  {path: 'login', component: LoginComponent},
  {path: 'signup', component: SignupComponent , canDeactivate: [ExitGuard]},
  {path: 'resetpassword', component: FogetPasswordComponent},
  {path: 'privatechat', component: Room2Component, canActivate: [ AuthGuard]},
  {path: 'room', component: RoomComponent, canActivate: [ AuthGuard]},
  {path: 'dashboard', component: DashboardComponent, canActivate: [ AuthGuard] },
  {path: '', redirectTo: '/login', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})

export class AppRoutingModule { }
export const routingComponents = [RoomComponent, SignupComponent , Room2Component,
   LoginComponent, FogetPasswordComponent, DashboardComponent];
