import { Component, OnInit } from '@angular/core';
import {SocketService} from './../../socket.service';
import { UserResponse } from './../../User/userface';
import { RoomService } from './../room.service';
import { UserService } from './../../User/user.service';
import { PCMESSAGE, PCMESSAGERESPONSE } from 'src/app/cap';

@Component({
  selector: 'app-room2',
  templateUrl: './room2.component.html',
  styleUrls: ['./room2.component.css']
})
export class Room2Component implements OnInit {


 allMessages: PCMESSAGERESPONSE[] = [];
  input: string;
  patData: UserResponse;
  User: UserResponse;

  constructor( private vice: SocketService, private roomvice: RoomService, private uservice: UserService ) { }


  getPatData() {
    if (localStorage.pcData) {
      return this.patData = JSON.parse(localStorage.pcData);
    }
    this.roomvice.PC$.subscribe((data: UserResponse) => this.patData = data);
  }

  getPcId(): string {
    if (this.User.username.toLowerCase() > this.patData.username.toLowerCase()) {
      return this.User._id + '_' + this.patData._id;
    } else {
      return this.patData._id  + '_' + this.User._id;
    }
  }

  getAllMessages() {
    const pcId: string = this.getPcId();
    this.roomvice.allPcMessages(pcId).subscribe( data => {
      if (data.length > 0) {
        this.allMessages = data;
      }
    });
  }

  getUserData() {
    this.uservice.USER$.subscribe( data => this.User = data);
  }

  sendMessage() {
    if (this.input) {
      const mess: PCMESSAGE = {
        message: this.input, pcId: this.getPcId(), type: 2, fromId: this.User._id, toId: this.patData._id, date: new Date() };
      this.vice.sendPcMessage(mess);
    }
    this.input = '';
  }

  sentMessages() {
    this.vice.getPcMessage().subscribe(data => {
     return this.allMessages.push(data);
    });
  }

  ngOnInit() {
    this.getPatData();
    this.getAllMessages();
    this.getUserData();
  }

}
