import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { UserService } from './../user.service';
import { Router } from '@angular/router';
import { GuardService } from 'src/app/guard/guard.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  processing = false;
  errorMessage: string;

  constructor(private  fb: FormBuilder, private uservice: UserService, private route: Router, private guard: GuardService) { }
  login = this.fb.group({
    email: ['', [Validators.required, Validators.email]],
    password: ['', [Validators.required]]
  });

  get Email() { return this.login.get('email'); }
  get Password() {return this.login.get('password'); }

  submitForm() {
    if (this.login.valid) {
      this.processing = true;
      console.log('processing');
      const form = { email: this.Email.value, password: this.Password.value };
      this.uservice.userLogin(form)
        .subscribe( (collector) => {
          if (collector._id) {
            console.log(collector);
            this.uservice.userData.next(collector);
            const url = this.guard.directedToUrl ? this.route.parseUrl(this.guard.directedToUrl) : '/dashboard';
            this.route.navigateByUrl(url);
          } else {
            console.log('cannot fetch user');
          }
      },
      (error) => {
        console.log('error data is' + error.code);
      });
      this.processing = false;
    } else {
      return this.errorMessage = 'All the fields are required';
    }
  }



  ngOnInit() {
  }

}
