import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject} from 'rxjs';
import * as io from 'socket.io-client';
import { MESSAGE, MessageResponse, JoinMessageResponse } from './cap';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { PCMESSAGE, PCMESSAGERESPONSE } from 'src/app/cap';

@Injectable({
  providedIn: 'root'
})


export class SocketService {
  room = false;
  message = new BehaviorSubject('default value');
  Message$ = this.message.asObservable();
  socket: any;

  constructor( private http: HttpClient) {
    this.socket = io('http://192.168.43.41:2050');
    console.log(this.socket.io);
  }

  typing(user: string) {
    if (user) {
      this.socket.emit('typing', user);
    }
  }

  getTyper() {
    return new Observable<string>((observer) => {
      this.socket.on('message', (data: string ) => {
        observer.next(data);
      });
    });
  }

  sendMessage(value: MESSAGE) {
    if (value) {
      this.socket.emit('message', value);
    }
  }

  sendPcMessage(value: PCMESSAGE) {
    if (value) {
      this.socket.emit('pc', value);
    }
  }

  getPcMessage() {
    return new Observable<PCMESSAGERESPONSE>((observer) => {
      this.socket.on('pc', (data: PCMESSAGERESPONSE ) => {
        console.log(data);
        observer.next(data);
      });
    });
  }

  getMessage() {
    return new Observable<MessageResponse>((observer) => {
      this.socket.on('message', (data: MessageResponse ) => {
        console.log(data);
        observer.next(data);
      });
    });
  }

  leave(member: MESSAGE) {
    if (member) {
      this.socket.emit('leave', member);
    }
  }

  leaveNotification() {
    return new Observable<MessageResponse>((observer) => {
      this.socket.on('leave', (data: MessageResponse ) => {
        observer.next(data);
      });
    });
  }

  join(message: MESSAGE) {
    if (message) {
      this.socket.emit('join', message);
    }
  }

  joinNotification() {
    return new Observable<JoinMessageResponse>((observer) => {
      this.socket.on('join', (data: JoinMessageResponse ) => { // to be change
        console.log(JSON.stringify(data));
        observer.next(data);
      });
    });
  }

  memberAdded(member: any ) {
    if (member) {
      this.socket.emit('memberAdded', member);
    }
  }

  addedNotification() {
    return new Observable<string>((observer) => {
      this.socket.on('memberadded', (data: string ) => {
        observer.next(data);
      });
    });
  }

}
